<template>
  <van-cell @click="$router.push(`/detail/${item.id}`)">
  <!-- 使用 title 插槽来自定义标题 -->
  <template #title>
    <div class="header"><img :src="item.avatar" alt="头像">
    <div class="right">
        <p class="title van-ellipsis">{{ item.stem }}</p>
        <p class="avatarInfo"><span>{{ item.creator }}</span> | <span>{{ item.createdAt }}</span></p>
    </div></div>

  </template>
  <template #label>
    <div class="body van-multi-ellipsis--l2" >
      {{ clearHtmlTag(item.content) }}
    </div>
    <div class="info"><span>点赞 {{item.likeCount}}</span> | <span>浏览 {{item.views}}</span></div>
  </template>
</van-cell>
</template>

<script>
export default {

  props: {
    item: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  methods: {
    clearHtmlTag (str) {
      // 匹配标签结构：/<[^>]+>/g    <p> </p> <div> </div>
      // 将所有的标签标识，替换成 ''
      return str.replace(/<[^>]+>/g, '')
    }
  }
}
</script>

<style scoped lang="less">
.header{
    display: flex;
    align-items: center;
    height: 40px;
    img{
        display: block;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        overflow: hidden;
        }
    .right {
      flex: 1;
      overflow: hidden;
      padding-left: 10px;
      p {
        flex: 1;
        margin: 0;
        line-height: 1.5;
        &.title {
          width: 280px;
        }
        &.avatarInfo {
          margin-top: 5px;
          font-size: 10px;
          color: #999;
        }

      }
    }

}
.body{

    overflow: hidden;
    font-size: 14px;
    color: #666;
    line-height: 1.6;
    margin-top: 10px;
}
.info{
    font-size: 12px;
    color: #999;
    margin-top: 10px;
}
</style>
