<template>
    <div>
     <nav class="nav">
      <a href="#" :class="{active: sorter ==='weight_desc'}" @click='changeSorter("weight_desc")'>推荐</a>
      <a href="#" :class="{active: sorter === null}" @click=changeSorter(null)>最新</a>
      <div class="logo" ><img src="@/assets/logo.png" alt=""></div>
     </nav>
     <div class="show">
      <van-cell value="内容" is-link>
  <!-- 使用 title 插槽来自定义标题 -->

</van-cell>
<van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad">

  <ArticleItem v-for="item in list" :key="item.id" :item="item"></ArticleItem>
</van-list>
     </div>
    </div>
  </template>

<script>
import ArticleItem from '@/components/article-item.vue'
import { getArticleList } from '@/api/article'
export default {
  name: 'IndexPage',

  components: {
    ArticleItem
  },
  data () {
    return {
      current: 1,
      sorter: 'weight_desc',
      loading: false,
      finished: false,
      list: []
    }
  },
  methods: {
    changeSorter (val) {
      this.sorter = val
    },
    async onLoad () {
      console.log('需要加载更多了, 应该要发送请求了...')

      const res = await getArticleList({ current: this.current, sorter: this.sorter })
      this.list.push(...res.data.data.rows)
      this.loading = false
      this.current++

      if (this.current > res.data.pageTotal) {
        this.finished = true
      }
    }
  }

}
</script>

<style scoped lang="less">
.nav{
  height: 44px;
  position: fixed;
  left: 0;
  top: 0 ;
  width: 100%;
  margin-bottom: 10px;
  z-index: 999;
  background-color: white;
  display: flex;
  align-content: center;

  > a{
    position: relative;
    color: #999;
    font-size: 14px;
    line-height: 44px;
    margin-left: 20px;
    transition: all 0.3s;
    &::after{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #222;
    transition: all 0.3s;
  }
  &.active {
    color:#222;
    &::after{
      width: 14px;
    }
  }
  }

  .logo{
    display: flex;
    flex:1;
    justify-content: flex-end;
    align-items: center;
    > img{
      width: 64px;
      height: 28px;
      display: block;
      margin-right: 10px;
    }
  }

}
</style>
