import request from '@/utils/request.js'

export const getArticleList = (data) => {
  return request.get('/h5/interview/query', {
    params: {
      current: data.current,
      pageSize: 10,
      sorter: data.sorter
    }
  }
  )
}

export const getDetail = (id) => {
  return request.get('/h5/interview/show', {
    params: {
      id: id
    }
  })
}

export const getInterview = (data) => {
  return request.get('/h5/interview/opt/list', {
    params: {
      page: data.page,
      pageSize: 10,
      optType: data.type
    }
  })
}

export const toggleInterView = (data) => {
  return request.post('/h5/interview/opt', {
    id: data.id,
    optType: data.type
  })
}
